.btn-callback {
  display: inline-flex;
  align-items: center;
  text-decoration-line: underline;
  &__icon {
    margin-left: 1.5rem;
  }
}

.btn-callback-decor {
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;

  width: 24.1rem;
  height: 5.4rem;
  padding-left: 2.7rem;

  font-family: @circe;

  border: 0.2rem solid @color-s;
  box-sizing: border-box;
  border-radius: 1rem;

  &__icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5.4rem;
    height: 5.4rem;
    margin-left: auto;

    background-color: @color-s;
    border-radius: 1rem;
  }

  &--long {
    width: 28.5rem;
    padding-left: 1.7rem;
  }
}

.btn-go-link {
  display: inline-flex;
  align-items: center;
  font-family: @circe;
  &__icon {
    // margin-left: 0.2rem;
    // margin-left: 0.4rem;
    margin-left: 1rem;
  }
}

.btn-decor-line {
  position: relative;
  display: flex;
  align-items: center;
  height: 8.3rem;
  padding-left: 3rem;
  font-size: 3rem;
  font-weight: @bold;
  &__icon {
    margin-left: 1rem;
  }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;

    width: 10rem;
    height: 8.3rem;

    background-image: url('../img/svg/red-line.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &--small {
    height: 4.8rem;
    padding-left: 2rem;
    font-size: 1.8rem;
    &::before {
      width: 5.1rem;
      height: 4.8rem;
      background-image: url('../img/svg/red-line-small.svg');
    }
  }

  &--big {
    height: 10.4rem;
    padding-left: 4rem;
    font-size: 4rem;
    &::before {
      width: 11.4rem;
      height: 9.4rem;
    }
  }
}

